// Read settings from file
async function readSettings(): Promise<typeof defaultSettings> {
  try {
    const data = await fs.readFile(SETTINGS_FILE, 'utf8')
    const stored = JSON.parse(data)
    // Merge with defaults to handle new settings
    return { ...defaultSettings, ...stored }
  } catch {
    return defaultSettings
  }
}

// Write settings to file
async function writeSettings(settings: typeof defaultSettings): Promise<void> {
  settings.lastUpdated = new Date().toISOString()
  await fs.writeFile(SETTINGS_FILE, JSON.stringify(settings, null, 2))
}

// GET - Retrieve settings
export async function GET(request: NextRequest) {
  try {
    const { searchParams } = new URL(request.url)
    const section = searchParams.get('section')
    
    const settings = await readSettings()
    
    // If specific section requested, return only that section
    if (section && section in settings) {
      return NextResponse.json({
        success: true,
        data: settings[section as keyof typeof settings],
        section,
        lastUpdated: settings.lastUpdated
      })
    }
    
    // Return all settings
    return NextResponse.json({
      success: true,
      data: settings,
      lastUpdated: settings.lastUpdated
    })
  } catch (error) {
    console.error('Error reading settings:', error)
    return NextResponse.json({
      success: false,
      error: 'Failed to read settings'
    }, { status: 500 })
  }
}

// POST - Update settings (partial update)
export async function POST(request: NextRequest) {
  try {
    const body = await request.json()
    const { section, data } = body
    
    if (!section || !data) {
      return NextResponse.json({
        success: false,
        error: 'Missing section or data'
      }, { status: 400 })
    }
    
    const settings = await readSettings()
    
    // Validate section exists
    if (!(section in settings)) {
      return NextResponse.json({
        success: false,
        error: `Invalid section: ${section}`
      }, { status: 400 })
    }
    
    // Merge data with existing section
    const existingSection = settings[section as keyof typeof settings]
    if (typeof existingSection === 'object' && existingSection !== null) {
      settings[section as keyof typeof defaultSettings] = {
        ...existingSection,
        ...data
      } as any
    } else {
      settings[section as keyof typeof defaultSettings] = data
    }
    
    await writeSettings(settings)
    
    return NextResponse.json({
      success: true,
      message: `Settings for ${section} updated successfully`,
      data: settings[section as keyof typeof settings],
      lastUpdated: settings.lastUpdated
    })
  } catch (error) {
    console.error('Error updating settings:', error)
    return NextResponse.json({
      success: false,
      error: 'Failed to update settings'
    }, { status: 500 })
  }
}

// PUT - Replace entire settings or section
export async function PUT(request: NextRequest) {
  try {
    const body = await request.json()
    const { section, data } = body
    
    if (!data) {
      return NextResponse.json({
        success: false,
        error: 'Missing data'
      }, { status: 400 })
    }
    
    const settings = await readSettings()
    
    if (section) {
      // Replace specific section
      if (!(section in settings)) {
        return NextResponse.json({
          success: false,
          error: `Invalid section: ${section}`
        }, { status: 400 })
      }
      settings[section as keyof typeof defaultSettings] = data
    } else {
      // Replace all settings (preserve defaults for missing keys)
      Object.assign(settings, data)
    }
    
    await writeSettings(settings)
    
    return NextResponse.json({
      success: true,
      message: section ? `Settings for ${section} replaced successfully` : 'All settings replaced successfully',
      lastUpdated: settings.lastUpdated
    })
  } catch (error) {
    console.error('Error replacing settings:', error)
    return NextResponse.json({
      success: false,
      error: 'Failed to replace settings'
    }, { status: 500 })
  }
}

// DELETE - Reset settings to defaults
export async function DELETE(request: NextRequest) {
  try {
    const { searchParams } = new URL(request.url)
    const section = searchParams.get('section')
    
    const settings = await readSettings()
    
    if (section) {
      // Reset specific section to defaults
      if (!(section in defaultSettings)) {
        return NextResponse.json({
          success: false,
          error: `Invalid section: ${section}`
        }, { status: 400 })
      }
      settings[section as keyof typeof defaultSettings] = defaultSettings[section as keyof typeof defaultSettings] as any
    } else {
      // Reset all settings to defaults
      Object.assign(settings, defaultSettings)
    }
    
    await writeSettings(settings)
    
    return NextResponse.json({
      success: true,
      message: section ? `Settings for ${section} reset to defaults` : 'All settings reset to defaults',
      data: section ? settings[section as keyof typeof settings] : settings,
      lastUpdated: settings.lastUpdated
    })
  } catch (error) {
    console.error('Error resetting settings:', error)
    return NextResponse.json({
      success: false,
      error: 'Failed to reset settings'
    }, { status: 500 })
  }
}
